let toWrite: string[] = [];

let writtingDelay: number;
let eraseDelay: number;
let betweenTexts: number;
let charIndex = 0;
let toWriteIndex = 0;
let spanElement: HTMLSpanElement;
let cursorElement: HTMLSpanElement;

const writting = () => {
	if (spanElement.textContent?.length !== toWrite[toWriteIndex].length) {
		deleteClass(cursorElement, "typing__cursor--blink");
		spanElement.textContent += toWrite[toWriteIndex].charAt(charIndex);
		charIndex++;
		setTimeout(writting, writtingDelay);
	} else {
		cursorElement.classList.add("typing__cursor--blink");
		setTimeout(erase, betweenTexts);
	}
};

const erase = () => {
	if (spanElement.textContent?.length !== 0) {
		deleteClass(cursorElement, "typing__cursor--blink");
		spanElement.textContent = toWrite[toWriteIndex].substring(0, charIndex - 1);
		charIndex--;
		setTimeout(erase, eraseDelay);
	} else {
		toWriteIndex === toWrite.length - 1 ? (toWriteIndex = 0) : toWriteIndex++;
		cursorElement.classList.add("typing__cursor--blink");
		setTimeout(writting, betweenTexts);
	}
};

const deleteClass = (element: HTMLElement, className: string) => {
	if (element.classList.contains(className)) element.classList.remove(className);
};

export const typingEffect = (
	typingDelay: number,
	deleteDelay: number,
	betweenText: number,
	text: string[],
	container: HTMLElement,
	cursor: HTMLElement
) => {
	writtingDelay = typingDelay;
	eraseDelay = deleteDelay;
	betweenTexts = betweenText;
	toWrite = text;
	spanElement = container;
	cursorElement = cursor;

	writting();
};
