import React, { useEffect } from "react";
import { typingEffect } from "./../utils/typingEffect";

interface IProps {}

const Hero: React.FC<IProps> = (props) => {
	const toWrite = ["Front-end developer", "Back-end developer", "Multiplatform developer"];
	let spanElement: HTMLSpanElement;
	let cursorElement: HTMLSpanElement;

	useEffect(() => {
		typingEffect(150, 100, 1000, toWrite, spanElement, cursorElement);
	});

	return (
		<div className='hero'>
			<div className='hero__text'>
				<div className='hero__text__title'>Oscar Sanz</div>
				<div className='hero__text__subtitle'>
					<span className='typing__text' ref={(r) => (spanElement = r as HTMLSpanElement)}></span>
					<span className='typing__cursor' ref={(r) => (cursorElement = r as HTMLSpanElement)}>
						&nbsp;
					</span>
				</div>
			</div>
			<div className='hero__device'>
				<div className='hero__device__mouse'></div>
				<div className='hero__device__mobile'></div>
			</div>
		</div>
	);
};

export default Hero;
