import React from "react";
import TimeLineEvent from "./TimeLineEvent";

interface IProps {}

const TimeLine: React.FC<IProps> = (props) => {
	return (
		<div className='time-line'>
			<TimeLineEvent year={1998} text='I say Hello to the World' classColor='gradient' />
			<TimeLineEvent year={2011} text='End of Secondary School, branch of Tech Sciences.' classColor='secondary-light' />
			<TimeLineEvent year={2017} text='End of High School, branch of Tech Sciences.' classColor='grey-bluish' />
			<TimeLineEvent year={2019} text='Higher Technical Certificate in Multi-Device Application Developments' classColor='grey-5' />
		</div>
	);
};

export default TimeLine;
