import React from "react";
import logo from "./../images/logo-dark.svg";

interface IProps {}

const Navigation: React.FC<IProps> = (props) => {
	return (
		<div className='navigation grid'>
			<input id='navigation__toggler__input' type='checkbox' />
			<img src={logo} alt='Oscar Sanz Logo' className='navigation__logo' />
			<label className='navigation__toggler' htmlFor='navigation__toggler__input'>
				<div className='navigation__toggler__line'></div>
				<div className='navigation__toggler__line'></div>
				<div className='navigation__toggler__line'></div>
			</label>
			<ul className='navigation__menu'>
				<li className='navigation__menu__item'>Bio</li>
				<li className='navigation__menu__item'>Skills</li>
				<li className='navigation__menu__item'>Contact</li>
			</ul>
		</div>
	);
};

export default Navigation;
