import React from "react";
import clsx from "clsx";

interface IProps {
	year: number;
	text: string;
	classColor: string;
}

const TimeLineEvent: React.FC<IProps> = ({ year, text, classColor }) => {
	return (
		<div className='time-line__event'>
			<div className='grid'>
				<div className='time-line__event__date-indicator'>
					<div className={clsx("time-line__event__indicator", `time-line__event__indicator--${classColor}`)}></div>
					<div className='time-line__event__date'>{year}</div>
				</div>
				<div className='time-line__event__text'>
					<p>{text}</p>
				</div>
			</div>
		</div>
	);
};

export default TimeLineEvent;
