import React from "react";
import clsx from "clsx";

interface IProps {
	text: string;
	modifierClass?: string;
	icon?: any;
	onClickHandler: Function;
}

const Button: React.FC<IProps> = ({ text, modifierClass, icon, onClickHandler }) => {
	return (
		<button className={clsx("btn", modifierClass && modifierClass)} onClick={() => onClickHandler()}>
			<div className='btn__text'>{text}</div>
			{icon && icon}
		</button>
	);
};

export default Button;
