import React from "react";
import Fade from "react-reveal/Fade";

interface IProps {
	name: string;
	icon: any;
	level: number;
}

const SkillCard: React.FC<IProps> = ({ name, icon, level }) => {
	const print = (filled: number) => {
		const empty = 5 - filled;
		const points: any[] = [];
		for (let i = 0; i < level; i++) {
			points.push(<div className='skill-card__info__point skill-card__info__point--filled'></div>);
		}
		for (let i = 0; i < empty; i++) {
			points.push(<div className='skill-card__info__point'></div>);
		}
		return points;
	};

	return (
		<div className='skill-card'>
			<div className='skill-card__logo'>
				<svg className='skill-card__logo__icon' xmlns='http://www.w3.org/2000/svg' role='img' viewBox='0 0 24 24'>
					<title>{name}</title>
					{icon}
				</svg>
			</div>
			<div className='skill-card__info'>
				<div className='skill-card__info__name'>{name}</div>
				<div className='skill-card__info__points'>
					{print(level).map((point, index) => (
						<div key={`${name}--${index}`}>{point}</div>
					))}
				</div>
			</div>
		</div>
	);
};

export default SkillCard;
