import React from "react";

interface IProps {
	placeholder: string;
	icon?: any;
	onChangeHandler: Function;
}

const TextInput: React.FC<IProps> = ({ placeholder, icon, onChangeHandler }) => {
	return (
		<div className='input input--text'>
			{icon && (
				<svg className='input__icon' viewBox='0 0 20 20'>
					{icon}
				</svg>
			)}
			<input type='text' name='searchSkill' id='searchSkill' placeholder={placeholder} onKeyUp={(event) => onChangeHandler(event)} />
		</div>
	);
};

export default TextInput;
